body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #004643c9;
  color: #eeeeee;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.App {
  background: #004643;
  margin: 20px auto;
  width: 90%;
  max-width: 700px;
  color: #fffffe;
}
.header {
  text-align: center;
  background: #0b4e4b;
}
.book-list {
  max-width: 700px;
  width: 90%;
  margin: 0 auto;
  background: #004643;
  
}
.book-detail {
  background: #0b4e4b;
  padding-left: 10px;
  width: 80%;
  margin: 0px auto;
  border-radius: 10px;
}
.book-detail:hover {
  text-decoration: line-through;
  opacity: 0.7;
 
}
.book-form {
  width: 90%;
  max-width: 700px;
  margin: 10px auto;
  padding: 5px;
}
.form-group {
  width: 80%;
  margin: 10px auto;
}
input[type="text"] {
  width: 100%;
  background: #eeeeee;
  border: 0;
  padding: 10px 20px;
  display: block;
  box-sizing: border-box;
  margin: 6px 0;
}
button {
  margin: 10px auto;
  padding: 6px 20px;
  display: block;
  background: #f9bc60;
  border: 10px;
}
